
import morphdom from 'morphdom';
import { Tag, Content, Renderer, createRenderer } from './tag';

export type StateTransition<S> = (s: S) => StateUpdateResult<S>;
export type StateUpdateResult<S> = null | S | DeferredUpdate<S> | {now: S, later: DeferredUpdate<S>}
export type DeferredUpdate<S> = Promise<(s: S) => StateUpdateResult<S> >

export type StateEventHandler<S> = <E extends Event>(s: S, e: E) => StateUpdateResult<S>

// const history: any[] = []; // for debugging
export async function mount<State>(el: HTMLElement, vi: View<State>, sm: StateManager<State>): Promise<void> {
    const root = sm.render(vi(sm.state));
    el.prepend(root);

    while (true) {
        const state = await new Promise<State>(r => {sm.trigger = r;});
        console.log(JSON.parse(JSON.stringify(state)));
        const newRoot = sm.render(vi(state));
        morphdom(root, newRoot);
    }
}

export type View<State> = (st: State) => Tag<State>;
export type SubView<State> = (st: State) => Content<State>;

type StateManager<S> = {
    state: S,
    trigger: (s: S) => void,
    update: (st: StateTransition<S>) => void,
    render: Renderer<S>,
}

export const initStateManager = <S>(start: S): StateManager<S> => {
    let sm = {
        state: start,
        trigger: (s: S) => {s;},
        update: (st) => {
            const res = st(sm.state);
            if (res == null) {
            } else if (res instanceof Promise) {
                res.then(sm.update);
            } else if ('now' in res && 'later' in res) {
                sm.state = res.now;
                sm.trigger(sm.state);
                res.later.then(sm.update);
            } else {
                sm.state = res;
                sm.trigger(sm.state);
            }
        }
    } as StateManager<S>;

    sm.render = createRenderer(
        (h: StateEventHandler<S>) =>
            (e: Event) => {
                console.log('event');
                sm.update((s: S) => h(s,e));
            },
    );

    return sm;
}
